@include font('pfdin-regular', 'pfdintextcondpro-regular');
@include font('pfdin-italic', 'pfdintextcondpro-italic');
@include font('tt0248', 'tt0248m');
@include font('tt0249', 'tt0249m');

body {
    background-color: #eee;
    min-width: 1260px;
    font-family: 'pfdin-regular', sans-serif; }

.container {
    width: 1260px;
    margin: auto; }

.clr {
  @include clr; }

a {
  text-decoration: none;
  &:hover {
    text-decoration: underline; }
  &:active {
    text-decoration: underline; } }

.text-center {
  text-align: center; }

.text-right {
  text-align: right; }

.mr40 {
  margin-right: 40px; }

.ml40 {
  margin-left: 40px; }

.section {
  & + .section {
    margin-top: 40px; }
  &__title {
    font-size: 30px;
    font-weight: 400;
    line-height: 40px;
    text-transform: uppercase;
    a {
      color: #444444;
      &:hover {
        color: #fc2db7;
        text-decoration: none; }
      &:active {
        color: #9f9f9f; } } }

  &__hr {
    border-top: 0;
    border-bottom: 1px solid #eee;
    &--pink {
      border-color: #fb2cb6; } } }

.btn {
  &--more {
    margin-top: 20px;
    padding: 10px 15px;
    color: #eeeeee;
    font-size: 22px;
    font-weight: 400;
    background-color: #828282;
    &:hover {
      background-color: #fc2db7; }
    &:active {
      background-color: #9f9f9f; } } }

.right-col {
  width: 820px;
  @include dib;
  &__title {
    color: #000000;
    font-size: 30px;
    font-weight: 400;
    text-transform: uppercase;
    margin-bottom: 30px; }
  &__hr {
    border-top: 0;
    border-bottom: 1px solid #9f9f9f; }
  .breadcrumb {
    margin-left: 0;
    margin-bottom: 25px; } }

.simple-page {
  .breadcrumb {
    margin-left: 0;
    margin-bottom: 25px; }
  &__title {
    color: #000000;
    font-size: 30px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 30px; }
  &__subtitle {
    color: #000000;
    font-size: 22px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 40px; }
  &__hr {
    border-top: 0;
    border-bottom: 1px solid #9f9f9f;
    margin-bottom: 20px; }
  &__section {
    a {
      color: black;
      text-decoration: underline;
      &:hover {
        text-decoration: none; }
      &:active {
        color: #9f9f9f; } } }
  .section__title {
    color: #444; }
  &__section-desc {
    margin-bottom: 25px; } }

.list {
  font-size: 16px;
  font-weight: 400;
  color: #000000;
  > li {
    margin-bottom: 10px;
    padding-left: 40px;
    padding-right: 40px; }
  &--unordered {
    li:before {
      content: 'â€¢';
      display: inline-block;
      vertical-align: baseline;
      color: #fc2db7;
      margin-right: 4px; } }
  &--ordered {
    counter-reset: list;
    > li {
      color: #000000;
      font-size: 22px;
      &:before {
        counter-increment: list;
        content: counter(list) '.';
        display: inline-block;
        vertical-align: baseline;
        color: #fc2db7;
        margin-right: 5px; }
      li {
        font-size: 16px; } } }
  &--simple {
    > li {
      font-size: 22px;
      li {
        font-size: 16px; } } }
  &__item {
    & + & {
      margin-top: 15px; }
    &--highlighted {
      background-color: #fff;
      color: #fc2db7;
      padding-top: 10px;
      padding-bottom: 15px;
      p {
        color: black;
        font-size: 16px; } } } }

